import React from 'react';
import classes from './Projects.module.css';
import Card from './Card/Card';
import ProjectDetails from './ProjectDetails';

const Projects = props => {

    //const projects = ['proj1','proj2','proj3'];
    const cards = ProjectDetails.map(proj => {
        //console.log(proj); 
        return <Card key={proj.id} name={proj.name} img={proj.image} technologies={proj.technologies} liveDemo={proj.liveDemo}/>
    });

    return(
        <div>
            <div className={classes.Projects}>
                <h1 style={{marginTop:'30px',marginBottom:'30px'}}>Projects</h1>
                <div className={classes.ProjectCards}>
                    {cards}
                </div>
            </div>
        </div>
    );
};

export default Projects;