import React from 'react';

import classes from './Card.module.css';

const Card = props => {
    
    let button = <div className={classes.CardButton}>More Info</div>;
    if(props.liveDemo !== '')
        button = <a href={props.liveDemo} target="blank"><div className={classes.CardButton}>Live Demo</div></a>

    return(
        <div className={classes.Card}>
            <div><img src={props.img} alt={props.name} className={classes.Img}/></div>
            <div>{props.name}</div>
            <p className={classes.Technologies}>{props.technologies}</p>
            {button}
        </div>
    );
};

export default Card;