import React from 'react';
import ProfilePic from './ProfilePic/ProfilePic';
import classes from './Home.module.css';

const Home = props => {

    const scrollToContact = () => {
        window.scrollTo({top:document.getElementById('contact').offsetTop,left:0,behavior:"smooth"});
    }

    const openResume = () => {
        let element = document.createElement('a');
        element.setAttribute('href', 'https://drive.google.com/file/d/1oRU6e-SuoPHxfURfgtu0XV6w5-RurP11/view?usp=sharing');
        element.setAttribute('target','blank');
        element.style.display = 'none';
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
    };

    return (
        <div className={classes.Home}>
            <ProfilePic />
            <div className={classes.HomeDetails}>
                <p className={classes.Name}>Srinivasa Dilip Polepalli</p>
                <p className={classes.Details}>
                    <span className={classes.Designation}>Software Engineer</span>
                    <span className={classes.Education}> - M.Sci, B.Eng</span>
                </p>
                <p className={classes.Social}>
                    <a href="https://www.linkedin.com/in/srinivasa-dilip-polepalli/" target="blank" className={classes.SocialIcons}><span className="fa fa-linkedin-square"></span></a>
                    <a href="https://www.facebook.com/dilip.srinivasa.5/" target="blank" className={classes.SocialIcons}><span className="fa fa-facebook-square"></span></a>
                    <a href="https://github.com/psdilip1995" target="blank" className={classes.SocialIcons}><span className="fa fa-github"></span></a>
                </p>
                <p className={classes.Buttons}>
                    <button className={classes.Button} onClick={scrollToContact}>Contact Me</button>
                    <button className={classes.Button} onClick={openResume}>Resume</button>
                </p>
            </div>
        </div>
    );
};

export default Home;