import React, { Component } from 'react';
import classes from './Contactme.module.css';
import axios from 'axios';

import Footer from './Footer/Footer';
import Loading from './Loading/Loading';

import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';

class Contactme extends Component {

    state={
        name: '',
        email: '',
        phone:'',
        message: '',
        loading: false,
        sentMessage: ''
    }

    onNameChange = event => this.setState({name: event.target.value});
    onMailChange = event => this.setState({email: event.target.value});
    onPhoneChange = event => this.setState({phone: event.target.value});
    onMessageChange = event => this.setState({message: event.target.value});

    onClearClick = () => {
        this.setState({
            name: '',
            email: '',
            phone: '',
            message: ''
        });
    };

    onFormSubmit = () => {
        //const send = alert('Form Data is \nName : '+this.state.name+'\nE Mail : '+this.state.email+'\nPhone : '+this.state.phone+'\nMessage : '+this.state.message+'\nSend Message?');
        confirmAlert({
            title: 'Send Message ?',
            message: 'Form Data is \nName : '+this.state.name+'\nE Mail : '+this.state.email+'\nPhone : '+this.state.phone+'\nMessage : '+this.state.message+'\n',
            buttons: [
                {
                    label: 'Yes',
                    onClick: () => {
                        this.setState({loading: true});
                        axios.post('https://personal-website-message-2d75a.firebaseio.com/messages.json',{
                                name:this.state.name,
                                email: this.state.email,
                                phone: this.state.phone,
                                message: this.state.message
                        })
                        .then(response => {
                            if(response.status === 200){
                                this.setState({loading: false});
                                this.setState({sentMessage: 'success'});
                            }else{
                                this.setState({loading: false});
                                this.setState({sentMessage: 'fail'})
                            }
                        });
                    }
                },
                {
                    label: 'No',
                    onClick: () => {}
                }
            ]
        });
    };

    dailPhone = () => {
        let element = document.createElement('a');
        element.setAttribute('href', 'tel:+16319480350');
        element.style.display = 'none';
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
    };

    sendMail = () => {
        let element = document.createElement('a');
        element.setAttribute('href', 'mailto:psdilip1995@gmail.com');
        element.style.display = 'none';
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
    };
    openLinkedin = () => {
        let element = document.createElement('a');
        element.setAttribute('href', 'https://www.linkedin.com/in/srinivasa-dilip-polepalli/');
        element.setAttribute('target','blank');
        element.style.display = 'none';
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
    };

    render(){

        let form = (
            <div className={classes.ContactForm}>
                    <p className={classes.FormLabel}>Name:</p>
                    <input className={classes.FormInput} type="text" placeholder="Name" value={this.state.name} onChange={this.onNameChange}/>
                    <p className={classes.FormLabel}>email:</p>
                    <input className={classes.FormInput} type="mail" placeholder="E Mail" value={this.state.email} onChange={this.onMailChange}/>
                    <p className={classes.FormLabel}>Phone:</p>
                    <input className={classes.FormInput} type="text" placeholder="Phone Number" value={this.state.phone} onChange={this.onPhoneChange}/>
                    <p className={classes.FormLabel}>Message:</p>
                    <textarea className={classes.FormTextArea} placeholder="Message" value={this.state.message} onChange={this.onMessageChange}></textarea>
                    <div className={classes.FormButtons}>
                        <div className={classes.ContactFormSubmit} onClick={this.onFormSubmit}>Submit</div>
                        <div className={classes.ContactFormSubmit} onClick={this.onClearClick}>Clear</div>
                    </div>
                </div>
        );

        if(this.state.loading)
            form = <Loading />;

        if(this.state.sentMessage==='success'){
            form = <div>
                <h2>Message Received Successfully!<br/>Thank you for contacting me!<br/>I will get in touch with you soon!</h2>
                <div className={classes.SendOtherMsg} onClick={() => {this.setState({sentMessage:''})}}>Send Another Message</div>
            </div>;
        }

        if(this.state.sentMessage==='Fail'){
            form = <div>
                <h2 style={{color:'red'}}>Message Sending Failed!</h2>
            </div>
        }


        return (
            <React.Fragment>
            <div className={classes.Contactme}>
                <h1 style={{marginTop:'50px'}}>Let's Get in Touch</h1>
                <div className={classes.ContactOptions}>
                    <div className={classes.ContactOption} onClick={this.dailPhone}><span style={{marginRight:'15px'}} className="fa fa-phone"></span> Phone</div>
                    <div className={classes.ContactOption} onClick={this.sendMail}><span style={{marginRight:'15px'}} className="fa fa-envelope"></span>Mail</div>
                    <div className={classes.ContactOption} onClick={this.openLinkedin}><span style={{marginRight:'15px'}} className="fa fa-linkedin-square"></span>Message</div>
                </div>
                <p className={classes.Or}>or</p>
                {form}
                <Footer />
            </div>
            </React.Fragment>
        );
    }
}

export default Contactme;